<template>
  <el-container>
    <el-main>
      <el-table :data="hospitalList">
        <el-table-column prop="id" label="医院ID" />
        <el-table-column prop="comName" label="医院名字" />
        <el-table-column prop="nickName" label="医院别名" />
        <el-table-column prop="grade" label="医院等级" />
        <el-table-column prop="address" label="医院地区" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>

      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>确认删除该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
import { hospitalInfo } from "../../../../data/hosNetwork";
export default {
  name: "hosTable",
  data() {
    return {
      hospitalList: Array(8).fill(hospitalInfo),
      dialogVisible: false,
      currentPage: 1
    };
  },
  methods: {
    handleClick() {
      this.$router.push("hospitalAdd?type=edit&id=66");
    },
    delMsg() {
      this.dialogVisible = true;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }    
  }
};
</script>
<style lang="less" scoped>
</style>