<template>
  <el-container>
    <el-header class="header">
      <div class="left">
        <div>
          医院名称：<el-input class="com-input" placeholder="请输入医院名称" v-model="comName" />
          医院等级：<el-select v-model="gradeValue" class="com-input" placeholder="全部">
            <el-option v-for="item in hosGrade" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
        <div class="area">
          <span class="text">地区：</span>
          <el-select class="com-input" v-model="proValue" placeholder="全部">
            <el-option v-for="item in province" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <el-select class="com-input" v-model="cityValue" placeholder="全部">
            <el-option v-for="item in city" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <el-select class="com-input" v-model="areaValue" placeholder="全部">
            <el-option v-for="item in area" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
      </div>
      <div class="right">
        <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
        <el-button type="primary" @click="addCom" class="btn">新增企业</el-button>
      </div>
    </el-header>
  </el-container>
</template>
<script>
import { hosGrade, areaProvince } from "../../../../data/hosNetwork";
import _ from "lodash";
export default {
  name: "hosOptions",
  data() {
    return {
      province: [],
      city: [],
      area: [],
      hosGrade,
      comName: "",
      gradeValue: "",
      proValue: "",
      cityValue: "",
      areaValue: ""
    };
  },
  mounted: function() {
    this.handleProvince();
  },
  methods: {
    addCom() {
      this.$router.push("hospitalAdd?type=add");
    },
    handleProvince() {
      //处理省份
      let province = _.map(areaProvince, (item, key) => {
        let obj = {};
        obj.value = item.value;
        obj.label = item.label;
        return obj;
      });
      this.province = province;
    }
  },
  watch: {
      proValue: function(curVal, oldVal){
          if( !curVal ) return;
          let citys = _.filter(areaProvince, (item, key) => {
            return item.value === curVal;
          });
          let city = _.map(citys[0].children, (item, key) => {
            let obj = {};
            obj.value = item.value;
            obj.label = item.label;
            return obj;
          });
          this.cityValue = '';
          this.areaValue = '';
          this.city = city;
      },
      cityValue: function(curVal, oldVal){
          if( !curVal ) return;
          let citys = _.filter(areaProvince, (item, key) => {
            return item.value === this.proValue;
          });
          let areas = _.filter(citys[0].children, (item, key) => {
            return item.value === curVal;
          });
          let area = _.map(areas[0].children, (item, key) => {
            let obj = {};
            obj.value = item.value;
            obj.label = item.label;
            return obj;
          });
          this.areaValue = '';
          this.area = area;
      }
  }
};
</script>
<style lang="less" scoped>
@import url("./../../../../assets/less/color.less");
.header {
  display: flex;
  text-align: left;
  border-bottom: 1px solid @border-gray;
  height: inherit !important;
  padding-bottom: 20px;
  .area {
    margin-top: 20px;
    .text {
      display: inline-block;
      text-indent: 2em;
    }
  }
  .com-input {
    width: 210px;
    margin-right: 20px;
  }
  .right {
    display: flex;
    align-items: center;
    .btn {
      margin-left: 20px;
    }
  }
}
</style>